body {
  margin: 0 auto;
  padding: 20px;
}

.ishidden {
  visibility: hidden;
  display: none;
}

.gallery {
  max-width: calc(100% - 60px) / 4;
  width: 100%;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

img {
  width: 300px;
  height: 200px;
}

.search-form {
  justify-content: center;
  gap: 20px;
  margin: 20px;
  display: flex;
}

.search-form button {
  cursor: pointer;
  border-color: #00f;
  border-radius: 10px;
}

.load-more {
  border-color: #00f;
  border-radius: 10px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

